'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/components/providers/AuthProvider';
import { useToast } from '@/context/ToastContext';
import { AppButton } from '@/components/ui/app-button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { CheckCircle2, Clock, AlertCircle, Phone, ArrowLeft, Loader2, Mail } from 'lucide-react';
import { getSupabase } from '@/lib/supabaseClient';
import { CONTACT_INFO } from '@/lib/constants';

export default function VendedorAtivacao() {
    const { user, authStatus, profileStatus, logout, refreshProfile } = useAuth();
    const router = useRouter();
    const { showToast } = useToast();

    const [paymentMethod, setPaymentMethod] = useState('');
    const [reference, setReference] = useState('');
    const [submitting, setSubmitting] = useState(false);

    useEffect(() => {
        if (authStatus === 'anonymous') {
            router.replace('/entrar');
        } else if (user?.status === 'approved') {
            router.replace('/vendedor');
        }
    }, [user, authStatus, router]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!paymentMethod || !reference) return;

        setSubmitting(true);
        try {
            const supabase = getSupabase();
            if (!supabase) throw new Error('Supabase não configurado');

            // 1. Criar a solicitação
            const { error: requestError } = await supabase
                .from('seller_requests')
                .insert({
                    user_id: user?.id,
                    payment_method: paymentMethod,
                    payment_reference: reference,
                    status: 'pending_review'
                });

            if (requestError) throw requestError;

            // 2. Atualizar status do perfil para 'pending_review' via API ou client se permitido
            // Tentamos via client, as policies devem permitir se auth.uid() = id
            const { error: profileError } = await supabase
                .from('profiles')
                .update({ status: 'pending_review' })
                .eq('id', user?.id);

            if (profileError) {
                console.error('Erro ao atualizar perfil, mas solicitação foi criada:', profileError);
            }

            showToast({ variant: 'success', title: 'Solicitação enviada!', description: 'Aguarde a validação do administrador.' });

            await refreshProfile();
        } catch (err: any) {
            showToast({
                variant: 'error',
                title: 'Erro ao enviar',
                description: err.message || 'Ocorreu um problema inesperado.'
            });
        } finally {
            setSubmitting(false);
        }
    };

    if (profileStatus === 'loading' || !user) {
        return (
            <div className="flex min-h-[60vh] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }

    const isPendingPayment = user.status === 'pending_payment' || user.status === 'pending';
    const isPendingReview = user.status === 'pending_review';
    const isRejected = user.status === 'rejected';

    return (
        <div className="container mx-auto max-w-2xl px-4 py-12">
            <AppButton variant="ghost" onClick={() => router.push('/')} className="mb-6 gap-2">
                <ArrowLeft className="h-4 w-4" />
                Sair da ativação
            </AppButton>

            {isPendingReview ? (
                <Card className="border-primary/20 shadow-xl overflow-hidden">
                    <div className="h-2 bg-primary animate-pulse" />
                    <CardHeader className="text-center pb-2">
                        <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-primary/10">
                            <Clock className="h-8 w-8 text-primary" />
                        </div>
                        <CardTitle className="text-2xl font-bold">Pagamento em Verificação</CardTitle>
                        <CardDescription className="text-base text-balance italic">
                            Obrigado! Já recebemos os seus dados de pagamento.
                        </CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-6 pt-4">
                        <div className="rounded-xl border bg-muted/30 p-6 text-center space-y-4">
                            <p className="text-sm text-muted-foreground leading-relaxed">
                                A nossa equipa está a validar a sua referência. Este processo costuma levar apenas alguns minutos em horário comercial.
                            </p>
                            <div className="flex flex-col items-center gap-1">
                                <span className="text-xs uppercase tracking-widest font-semibold text-muted-foreground">Estado Atual</span>
                                <span className="inline-flex items-center gap-1.5 rounded-full bg-yellow-100 px-3 py-1 text-xs font-bold text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400 border border-yellow-200 dark:border-yellow-800">
                                    <div className="h-1.5 w-1.5 rounded-full bg-yellow-500 animate-pulse" />
                                    EM REVISÃO
                                </span>
                            </div>
                        </div>

                        <div className="space-y-4 border-t pt-6">
                            <h4 className="text-sm font-semibold flex items-center gap-2">
                                <Phone className="h-4 w-4 text-primary" />
                                Precisa de ajuda urgente?
                            </h4>
                            <p className="text-xs text-muted-foreground">
                                Se o seu pagamento não for validado em 1 hora, contacte o nosso suporte via WhatsApp enviando o comprovativo.
                            </p>
                        </div>
                    </CardContent>
                    <CardFooter className="bg-muted/30 border-t flex flex-col sm:flex-row gap-3 p-6">
                        <AppButton variant="primary" className="w-full gap-2 font-bold" asChild>
                            <a href={`https://wa.me/${CONTACT_INFO.WHATSAPP_RAW}`} target="_blank" rel="noopener noreferrer">
                                <Phone className="h-4 w-4" /> WhatsApp Admin
                            </a>
                        </AppButton>
                        <AppButton variant="outline" className="w-full gap-2" asChild>
                            <a href={`mailto:${CONTACT_INFO.EMAIL}`}>
                                <Mail className="h-4 w-4" /> Suporte Email
                            </a>
                        </AppButton>
                    </CardFooter>
                </Card>
            ) : (
                <Card className="border-t-4 border-t-primary shadow-xl">
                    <CardHeader>
                        <div className="flex items-center justify-between">
                            <div>
                                <CardTitle className="text-2xl font-bold">Ativação de Vendedor</CardTitle>
                                <CardDescription className="mt-1">
                                    Taxa única de ativação: <b className="text-primary font-black">20 MT</b>
                                </CardDescription>
                            </div>
                            <div className="h-12 w-12 rounded-xl bg-primary/10 flex items-center justify-center">
                                <CheckCircle2 className="h-6 w-6 text-primary" />
                            </div>
                        </div>
                    </CardHeader>
                    <CardContent className="space-y-8">
                        {isRejected && (
                            <div className="rounded-lg border border-destructive/20 bg-destructive/5 p-4 flex gap-3">
                                <AlertCircle className="h-5 w-5 text-destructive shrink-0" />
                                <div>
                                    <p className="text-sm font-semibold text-destructive">Pagamento Rejeitado</p>
                                    <p className="text-xs text-destructive/80 mt-1">
                                        Motivo: {user.adminNote || 'Referência inválida ou pagamento não recebido.'}
                                    </p>
                                </div>
                            </div>
                        )}

                        <div className="space-y-4">
                            <h3 className="font-semibold text-foreground flex items-center gap-2">
                                <div className="h-5 w-5 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-[10px]">1</div>
                                Instruções de Pagamento
                            </h3>
                            <div className="grid gap-3 sm:grid-cols-2">
                                <div className="rounded-xl border bg-muted/50 p-4 space-y-2 text-center">
                                    <p className="text-xs font-bold text-muted-foreground uppercase tracking-wider">M-Pesa / Emola</p>
                                    <p className="text-sm font-mono font-bold text-primary">{CONTACT_INFO.WHATSAPP}</p>
                                    <p className="text-[10px] text-muted-foreground">Nome: CeluPublic / Administrador</p>
                                </div>
                                <div className="rounded-xl border bg-muted/50 p-4 space-y-2 text-center">
                                    <p className="text-xs font-bold text-muted-foreground uppercase tracking-wider">Dúvidas?</p>
                                    <p className="text-sm font-bold text-primary">{CONTACT_INFO.EMAIL}</p>
                                    <p className="text-[10px] text-muted-foreground">Contacte-nos para apoio.</p>
                                </div>
                            </div>
                        </div>

                        <form onSubmit={handleSubmit} className="space-y-6">
                            <h3 className="font-semibold text-foreground flex items-center gap-2">
                                <div className="h-5 w-5 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-[10px]">2</div>
                                Confirmação de Pagamento
                            </h3>

                            <div className="space-y-4 bg-muted/20 p-5 rounded-2xl border">
                                <div className="space-y-2">
                                    <Label htmlFor="method">Método Utilizado</Label>
                                    <select
                                        id="method"
                                        value={paymentMethod}
                                        onChange={(e) => setPaymentMethod(e.target.value)}
                                        required
                                        className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                                    >
                                        <option value="">Selecione o método</option>
                                        <option value="M-Pesa">M-Pesa</option>
                                        <option value="Emola">Emola</option>
                                        <option value="Conta Móvel">Conta Móvel</option>
                                        <option value="Banco">Transferência Bancária</option>
                                    </select>
                                </div>

                                <div className="space-y-2">
                                    <Label htmlFor="ref">ID de Transação / Referência</Label>
                                    <Input
                                        id="ref"
                                        placeholder="Ex: 8XJ2K9L..."
                                        value={reference}
                                        onChange={(e) => setReference(e.target.value)}
                                        required
                                        className="bg-background"
                                    />
                                    <p className="text-[10px] text-muted-foreground italic">
                                        Insira o código que recebeu por SMS após o pagamento.
                                    </p>
                                </div>
                            </div>

                            <AppButton
                                type="submit"
                                className="w-full h-12 text-base font-bold transition-all hover:scale-[1.02]"
                                disabled={submitting || !paymentMethod || !reference}
                            >
                                {submitting ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : null}
                                {submitting ? 'A enviar...' : 'Confirmar Pagamento'}
                            </AppButton>
                        </form>
                    </CardContent>
                    <CardFooter className="flex flex-col gap-3 pt-2">
                        <AppButton variant="ghost" size="sm" onClick={() => logout()} className="w-full text-muted-foreground mt-4">
                            Sair da conta
                        </AppButton>
                    </CardFooter>
                </Card>
            )}
        </div>
    );
}
